/*global media queries*/
#roundtripWizard .q-stepper__content {
  margin-top: -40px;
}
#roundtripWizard .q-stepper__step-inner {
  padding-top: 0;
}
#roundtripWizard .sticky-navigation {
  position: -webkit-sticky;
  position: sticky;
  bottom: 0;
  z-index: 100;
  background-color: white;
  padding: 10px 20px 10px 0;
  box-shadow: 0 -5px 5px -5px rgba(0, 0, 0, 0.19);
  display: flex;
  justify-content: flex-end;
}
#roundtripWizard .sticky-navigation > * {
  padding-top: 0;
}
#roundtripWizard .auto-route-btn {
  display: inline-block;
}
#roundtripWizard .flip-list-move {
  transition: transform 0.8s ease;
}
@media only screen and (min-width: 0px) and (max-width: 719px) {
  #roundtripWizard .q-stepper--horizontal .q-stepper__step-inner {
    padding: 8px;
  }
  #roundtripWizard .auto-route-btn {
    padding-left: 10px;
  }
}
.MsoListParagraph {
  display: inline !important;
}
